.radio-select {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin: 12px 0;
  border: none;
  background: none; }
  .radio-select:focus {
    outline: none;
    color: #007cba; }
  .radio-select--active {
    color: #007cba; }
    .radio-select--active g {
      fill: #007cba !important; }
  .radio-select__label {
    margin-left: 7px;
    font-size: 14px; }

